___CSS_LOADER_EXPORT___.push([module.id, `/* Direct Messages Modal - Mejoras de contraste y animaciones */

/* Overlay con mejor contraste */
.dm-modal-overlay {
    background-color: rgba(0, 0, 0, 0.75) !important;
    backdrop-filter: blur(4px);
    animation: fadeIn 0.2s ease-out;
    pointer-events: all !important;
    opacity: 1 !important;
}

/* Modal container con animacin */
.dm-modal-container {
    background: var(--surface-primary, #1a1a1a) !important;
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
    animation: slideUp 0.3s ease-out;
}

/* Animaciones */
@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slideUp {
    from {
        transform: translateY(20px);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* Transicin al cerrar */
.dm-modal-overlay.closing {
    animation: fadeOut 0.2s ease-out;
}

.dm-modal-container.closing {
    animation: slideDown 0.2s ease-out;
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@keyframes slideDown {
    from {
        transform: translateY(0);
        opacity: 1;
    }

    to {
        transform: translateY(20px);
        opacity: 0;
    }
}

/* Modern Table - Separadores minimalistas */
.modern-table table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
}

.modern-table thead th {
    padding: 12px 16px;
    text-align: left;
    font-weight: 600;
    font-size: 0.75rem;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    color: var(--text-secondary, #999);
    border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.modern-table tbody tr {
    transition: background-color 0.15s ease;
}

.modern-table tbody tr:hover {
    background-color: rgba(255, 255, 255, 0.02);
}

.modern-table tbody td {
    padding: 12px 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.04);
    font-size: 0.875rem;
}

.modern-table tbody tr:last-child td {
    border-bottom: none;
}

/* Textos cortos en cliente */
.client-info strong {
    display: block;
    font-size: 0.875rem;
    color: var(--text-primary, #fff);
}

.policy-num {
    font-family: 'Courier New', monospace;
    font-size: 0.8125rem;
    color: var(--text-secondary, #999);
}

/* Botones de cono */
.btn-icon {
    padding: 6px;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.15s ease;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.btn-icon:hover {
    background: rgba(255, 255, 255, 0.05);
    border-color: rgba(255, 255, 255, 0.2);
}

.btn-icon svg {
    width: 16px;
    height: 16px;
    color: var(--text-secondary, #999);
}

/* Badges de estado compactos */
.text-danger {
    color: #ef5350;
    font-size: 0.75rem;
    font-weight: 600;
}

.text-warning {
    color: #ffa726;
    font-size: 0.75rem;
    font-weight: 600;
}

/* Action buttons row */
.action-buttons {
    display: flex;
    gap: 4px;
    justify-content: flex-end;
}`, "",{"version":3,"sources":["webpack://./styles/dm-modal-improvements.css"],"names":[],"mappings":"AAAA,+DAA+D;;AAE/D,gCAAgC;AAChC;IACI,gDAAgD;IAChD,0BAA0B;IAC1B,+BAA+B;IAC/B,8BAA8B;IAC9B,qBAAqB;AACzB;;AAEA,kCAAkC;AAClC;IACI,sDAAsD;IACtD,0CAA0C;IAC1C,yCAAyC;IACzC,gCAAgC;AACpC;;AAEA,gBAAgB;AAChB;IACI;QACI,UAAU;IACd;;IAEA;QACI,UAAU;IACd;AACJ;;AAEA;IACI;QACI,2BAA2B;QAC3B,UAAU;IACd;;IAEA;QACI,wBAAwB;QACxB,UAAU;IACd;AACJ;;AAEA,yBAAyB;AACzB;IACI,gCAAgC;AACpC;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI;QACI,UAAU;IACd;;IAEA;QACI,UAAU;IACd;AACJ;;AAEA;IACI;QACI,wBAAwB;QACxB,UAAU;IACd;;IAEA;QACI,2BAA2B;QAC3B,UAAU;IACd;AACJ;;AAEA,4CAA4C;AAC5C;IACI,WAAW;IACX,yBAAyB;IACzB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;IAChB,kBAAkB;IAClB,yBAAyB;IACzB,sBAAsB;IACtB,kCAAkC;IAClC,kDAAkD;AACtD;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,kBAAkB;IAClB,kDAAkD;IAClD,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA,6BAA6B;AAC7B;IACI,cAAc;IACd,mBAAmB;IACnB,gCAAgC;AACpC;;AAEA;IACI,qCAAqC;IACrC,oBAAoB;IACpB,kCAAkC;AACtC;;AAEA,qBAAqB;AACrB;IACI,YAAY;IACZ,uBAAuB;IACvB,0CAA0C;IAC1C,kBAAkB;IAClB,eAAe;IACf,0BAA0B;IAC1B,oBAAoB;IACpB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,qCAAqC;IACrC,sCAAsC;AAC1C;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kCAAkC;AACtC;;AAEA,+BAA+B;AAC/B;IACI,cAAc;IACd,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA,uBAAuB;AACvB;IACI,aAAa;IACb,QAAQ;IACR,yBAAyB;AAC7B","sourcesContent":["/* Direct Messages Modal - Mejoras de contraste y animaciones */\r\n\r\n/* Overlay con mejor contraste */\r\n.dm-modal-overlay {\r\n    background-color: rgba(0, 0, 0, 0.75) !important;\r\n    backdrop-filter: blur(4px);\r\n    animation: fadeIn 0.2s ease-out;\r\n    pointer-events: all !important;\r\n    opacity: 1 !important;\r\n}\r\n\r\n/* Modal container con animacin */\r\n.dm-modal-container {\r\n    background: var(--surface-primary, #1a1a1a) !important;\r\n    border: 1px solid rgba(255, 255, 255, 0.1);\r\n    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);\r\n    animat