 */\r\n\r\n/* ============================================================================\r\n   BASE MODAL OVERLAY - Overlay comn para todos los modales\r\n   ============================================================================ */\r\n\r\n.modal-overlay,\r\n.app-modal-overlay,\r\n.chart-modal-overlay {\r\n    position: fixed;\r\n    inset: 0;\r\n    width: 100vw;\r\n    height: 100vh;\r\n    z-index: 9999;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    background: rgba(0, 0, 0, 0.6);\r\n    backdrop-filter: blur(6px);\r\n    -webkit-backdrop-filter: blur(6px);\r\n    opacity: 0;\r\n    pointer-events: none;\r\n    transition: opacity 0.3s ease;\r\n    padding: 20px;\r\n    overflow-y: auto;\r\n}\r\n\r\n/* Design tokens for modal accents and section highlights (light + dark friendly) */\r\n:root {\r\n    --modal-accent-height: 6px;\r\n    /* Default accent RGB (light theme fallback) - matches light primary #0d3d23 */\r\n    --primary-rgb: 13, 61, 35;\r\n    /* Secondary surface used inside modal cards */\r\n    --bg-secondary-rgb: 245, 245, 245;\r\n}\r\n\r\n/* Dark theme overrides for accent and surfaces */\r\nhtml[data-theme=\"dark-forest\"],\r\nbody.dark-forest {\r\n    --primary-rgb: 47, 107, 122;\r\n    /* dark-forest accent fallback */\r\n    --bg-secondary-rgb: 6, 24, 20;\r\n}\r\n\r\n/* Accent strip at top of every modal (applies even when markup doesn't include an explicit accent element) */\r\n.modal-content::before,\r\n.app-modal::before,\r\n.chart-modal-content::before {\r\n    content: \"\";\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    height: var(--modal-accent-height);\r\n    background: linear-gradient(90deg, rgba(var(--primary-rgb), 0.95), rgba(var(--primary-rgb), 0.55));\r\n    border-top-left-radius: inherit;\r\n    border-top-right-radius: inherit;\r\n    z-index: 2;\r\n    pointer-events: none;\r\n}\r\n\r\n/* Ensure header sits above the accent strip */\r\n.modal-header,\r\n.app-modal-header,\r\n.chart-modal-header {\r\n    position: relative;\r\n    z-index: 3;\r\n}\r\n\r\n/* A thin explicit accent element (used by some modals) */\r\n.modal-accent {\r\n    height: var(--modal-accent-height);\r\n    width: 100%;\r\n    background: linear-gradient(90deg, rgba(var(--primary-rgb), 0.95), rgba(var(--primary-rgb), 0.55));\r\n}\r\n\r\n/* Modal icon container for a consistent look */\r\n.modal-icon {\r\n    width: 40px;\r\n    height: 40px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    border-radius: 8px;\r\n    background: linear-gradient(180deg, rgba(var(--primary-rgb), 0.08), rgba(var(--primary-rgb), 0.04));\r\n    flex-shrink: 0;\r\n}\r\n\r\n/* Policy/section card look used throughout the modal content */\r\n.policy-section {\r\n    padding: 0.75rem;\r\n    border-radius: 10px;\r\n    background: linear-gradient(180deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.01));\r\n    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.06);\r\n    border-left: 4px solid rgba(var(--primary-rgb), 0.12);\r\n    padding-left: 0.9rem;\r\n    margin-bottom: 1rem;\r\n}\r\n\r\nbody.dark-forest .policy-section,\r\nhtml[data-theme=\"dark-forest\"] .policy-section {\r\n    background: linear-gradient(180deg, rgba(255, 255, 255, 0.015), rgba(255, 255, 255, 0.01));\r\n    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.45);\r\n    border-left-color: rgba(var(--primary-rgb), 0.16);\r\n}\r\n\r\n/* Consistent item cards inside lists */\r\n.receipt-item,\r\n.claim-item,\r\n.comment-item {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 0.6rem;\r\n    border-radius: 8px;\r\n    background: rgba(var(--bg-secondary-rgb), 0.45);\r\n    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.06);\r\n    margin-bottom: 0.6rem;\r\n}\r\n\r\nbody.dark-forest .receipt-item,\r\nbody.dark-forest .claim-item,\r\nbody.dark-forest .comment-item {\r\n    background: rgba(var(--bg-secondary-rgb), 0.28);\r\n    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.45);\r\n}\r\n\r\n/* Section divider used between modal segments */\r\n.section-divider {\r\n    height: 1px;\r\n    background: linear-gradient(90deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.005), rgba(255, 255, 255, 0.02));\r\n    margin: 1rem 0;\r\n}\r\n\r\n.modal-overlay.active,\r\n.app-modal-overlay.active,\r\n.chart-modal-overlay.active {\r\n    opacity: 1;\r\n    pointer-events: all;\r\n}\r\n\r\n/* Dark Forest Theme - Overlay */\r\nbody.dark-forest .modal-overlay,\r\nbody.dark-forest .app-modal-overlay,\r\nbody.dark-forest .chart-modal-overlay,\r\nhtml[data-theme=\"dark-forest\"] .modal-overlay,\r\nhtml[data-theme=\"dark-forest\"] .app-modal-overlay,\r\nhtml[data-theme=\"dark-forest\"] .chart-modal-overlay {\r\n    background: rgba(6, 24, 20, 0.92);\r\n    backdrop-filter: blur(12px) saturate(160%);\r\n    -webkit-backdrop-filter: blur(12px) saturate(160%);\r\n}\r\n\r\n/* ============================================================================\r\n   MODAL CONTENT CONTAINER - Contenedor del modal\r\n   ============================================================================ */\r\n\r\n.modal-content,\r\n.app-modal,\r\n.chart-modal-content {\r\n    position: relative;\r\n    width: 90%;\r\n    max-width: 800px;\r\n    max-height: 90vh;\r\n    background: #ffffff;\r\n    border-radius: 16px;\r\n    box-shadow:\r\n        0 24px 48px rgba(0, 0, 0, 0.2),\r\n        0 0 0 1px rgba(0, 0, 0, 0.1);\r\n    display: flex;\r\n    flex-direction: column;\r\n    overflow: hidden;\r\n    transform: scale(0.9) translateY(20px);\r\n    transition: transform 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);\r\n}\r\n\r\n.modal-overlay.active .modal-content,\r\n.app-modal-overlay.active .app-modal,\r\n.chart-modal-overlay.active .chart-modal-content {\r\n    transform: scale(1) translateY(0);\r\n}\r\n\r\n/* Modal sizes */\r\n.app-modal-sm {\r\n    max-width: 500px;\r\n}\r\n\r\n.app-modal-md {\r\n    max-width: 700px;\r\n}\r\n\r\n.app-modal-lg {\r\n    max-width: 900px;\r\n}\r\n\r\n.app-modal-xl {\r\n    max-width: 1200px;\r\n}\r\n\r\n/* Dark Forest Theme - Modal Content */\r\nbody.dark-forest .modal-content,\r\nbody.dark-forest .app-modal,\r\nbody.dark-forest .chart-modal-content,\r\nhtml[data-theme=\"dark-forest\"] .modal-content,\r\nhtml[data-theme=\"dark-forest\"] .app-modal,\r\nhtml[data-theme=\"dark-forest\"] .chart-modal-content {\r\n    background: var(--theme-surface-bg-solid, rgba(8, 28, 24, 0.94));\r\n    border: 1px solid var(--theme-surface-border, rgba(223, 243, 237, 0.32));\r\n    box-shadow:\r\n        0 24px 48px rgba(0, 0, 0, 0.7),\r\n        0 0 0 1px var(--theme-surface-border-strong, rgba(223, 243, 237, 0.42)),\r\n        0 0 60px var(--theme-accent-shadow-soft, rgba(47, 107, 122, 0.18));\r\n}\r\n\r\n/* ============================================================================\r\n   MODAL HEADER - Cabecera del modal\r\n   ============================================================================ */\r\n\r\n.modal-header,\r\n.app-modal-header,\r\n.chart-modal-header {\r\n    padding: 24px 24px 20px;\r\n    border-bottom: 1px solid rgba(0, 0, 0, 0.1);\r\n    background: #f8f9fa;\r\n    flex-shrink: 0;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n}\r\n\r\n.modal-header h2,\r\n.app-modal-title,\r\n.chart-modal-title {\r\n    margin: 0;\r\n    font-size: 1.375rem;\r\n    font-weight: 700;\r\n    color: #1a1a1a;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n    line-height: 1.4;\r\n}\r\n\r\n.modal-body h3,\r\n.app-modal-body h3,\r\n.chart-modal-body h3 {\r\n    font-size: 1.125rem;\r\n    font-weight: 600;\r\n    color: #333;\r\n    margin: 1.5rem 0 1rem;\r\n}\r\n\r\n.modal-body p,\r\n.app-modal-body p,\r\n.chart-modal-body p {\r\n    font-size: 0.938rem;\r\n    line-height: 1.6;\r\n    color: #444;\r\n    margin: 0.5rem 0;\r\n}\r\n\r\n.modal-body label,\r\n.app-modal-body label,\r\n.chart-modal-body label {\r\n    font-size: 0.875rem;\r\n    font-weight: 500;\r\n    color: #555;\r\n}\r\n\r\n[data-theme=\"dark-forest\"] .modal-body h3,\r\n[data-theme=\"dark-forest\"] .app-modal-body h3,\r\n[data-theme=\"dark-forest\"] .chart-modal-body h3 {\r\n    color: #e0e0e0;\r\n}\r\n\r\n[data-theme=\"dark-forest\"] .modal-body p,\r\n[data-theme=\"dark-forest\"] .app-modal-body p,\r\n[data-theme=\"dark-forest\"] .chart-modal-body p {\r\n    color: #d0d0d0;\r\n}\r\n\r\n[data-theme=\"dark-forest\"] .modal-body label,\r\n[data-theme=\"dark-forest\"] .app-modal-body label,\r\n[data-theme=\"dark-forest\"] .chart-modal-body label {\r\n    color: #b0b0b0;\r\n}\r\n\r\n/* Dark Forest Theme - Modal Header */\r\nbody.dark-forest .modal-header,\r\nbody.dark-forest .app-modal-header,\r\nbody.dark-forest .chart-modal-header,\r\nhtml[data-theme=\"dark-forest\"] .modal-header,\r\nhtml[data-theme=\"dark-forest\"] .app-modal-header,\r\nhtml[data-theme=\"dark-forest\"] .chart-modal-header {\r\n    background: var(--theme-surface-bg-deep, #061c17);\r\n    border-bottom-color: var(--theme-surface-border, rgba(223, 243, 237, 0.32));\r\n}\r\n\r\nbody.dark-forest .modal-header h2,\r\nbody.dark-forest .app-modal-title,\r\nbody.dark-forest .chart-modal-title,\r\nhtml[data-theme=\"dark-forest\"] .modal-header h2,\r\nhtml[data-theme=\"dark-forest\"] .app-modal-title,\r\nhtml[data-theme=\"dark-forest\"] .chart-modal-title {\r\n    color: var(--theme-text-primary, #f0f7f3);\r\n    text-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);\r\n}\r\n\r\n/* ============================================================================\r\n   MODAL CLOSE BUTTON - Botn de cerrar\r\n   ============================================================================ */\r\n\r\n.modal-close,\r\n.app-modal-close,\r\n.chart-modal-close {\r\n    width: 36px;\r\n    height: 36px;\r\n    padding: 0;\r\n    background: transparent;\r\n    border: 1px solid rgba(0, 0, 0, 0.1);\r\n    border-radius: 8px;\r\n    color: #555;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    font-size: 24px;\r\n    line-height: 1;\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.modal-close:hover,\r\n.app-modal-close:hover,\r\n.chart-modal-close:hover {\r\n    background: rgba(0, 0, 0, 0.05);\r\n    color: #1a1a1a;\r\n    transform: rotate(90deg);\r\n}\r\n\r\n/* Dark Forest Theme - Close Button */\r\nbody.dark-forest .modal-close,\r\nbody.dark-forest .app-modal-close,\r\nbody.dark-forest .chart-modal-close,\r\nhtml[data-theme=\"dark-forest\"] .modal-close,\r\nhtml[data-theme=\"dark-forest\"] .app-modal-close,\r\nhtml[data-theme=\"dark-forest\"] .chart-modal-close {\r\n    background: var(--theme-accent-bg-soft, rgba(47, 107, 122, 0.22));\r\n    border-color: var(--theme-accent-border-subtle, rgba(47, 107, 122, 0.24));\r\n    color: var(--theme-text-secondary, #c8ddd2);\r\n}\r\n\r\nbody.dark-forest .modal-close:hover,\r\nbody.dark-forest .app-modal-close:hover,\r\nbody.dark-forest .chart-modal-close:hover,\r\nhtml[data-theme=\"dark-forest\"] .modal-close:hover,\r\nhtml[data-theme=\"dark-forest\"] .app-modal-close:hover,\r\nhtml[data-theme=\"dark-forest\"] .chart-modal-close:hover {\r\n    background: var(--theme-accent-color, #2f6b7a);\r\n    border-color: var(--theme-accent-color-hover, #45a0b8);\r\n    color: var(--theme-text-primary, #f0f7f3);\r\n    box-shadow: 0 0 16px var(--theme-accent-shadow-soft, rgba(47, 107, 122, 0.18));\r\n}\r\n\r\n/* ============================================================================\r\n   MODAL BODY - Cuerpo del modal (scrollable)\r\n   ============================================================================ */\r\n\r\n.modal-body,\r\n.app-modal-body,\r\n.chart-modal-body {\r\n    flex: 1;\r\n    overflow-y: auto;\r\n    overflow-x: hidden;\r\n    padding: 24px;\r\n    background: #ffffff;\r\n    -webkit-overflow-scrolling: touch;\r\n    scroll-behavior: smooth;\r\n}\r\n\r\n/* Dark Forest Theme - Modal Body */\r\nbody.dark-forest .modal-body,\r\nbody.dark-forest .app-modal-body,\r\nbody.dark-forest .chart-modal-body,\r\nhtml[data-theme=\"dark-forest\"] .modal-body,\r\nhtml[data-theme=\"dark-forest\"] .app-modal-body,\r\nhtml[data-theme=\"dark-forest\"] .chart-modal-body {\r\n    background: var(--theme-surface-bg-solid, rgba(8, 28, 24, 0.94));\r\n    color: var(--theme-text-primary, #f0f7f3);\r\n}\r\n\r\n/* Custom scrollbar for modal body */\r\n.modal-body::-webkit-scrollbar,\r\n.app-modal-body::-webkit-scrollbar,\r\n.chart-modal-body::-webkit-scrollbar {\r\n    width: 8px;\r\n}\r\n\r\n.modal-body::-webkit-scrollbar-track,\r\n.app-modal-body::-webkit-scrollbar-track,\r\n.chart-modal-body::-webkit-scrollbar-track {\r\n    background: transparent;\r\n}\r\n\r\n.modal-body::-webkit-scrollbar-thumb,\r\n.app-modal-body::-webkit-scrollbar-thumb,\r\n.chart-modal-body::-webkit-scrollbar-thumb {\r\n    background: rgba(0, 0, 0, 0.2);\r\n    border-radius: 4px;\r\n}\r\n\r\n.modal-body::-webkit-scrollbar-thumb:hover,\r\n.app-modal-body::-webkit-scrollbar-thumb:hover,\r\n.chart-modal-body::-webkit-scrollbar-thumb:hover {\r\n    background: rgba(0, 0, 0, 0.3);\r\n}\r\n\r\n/* Dark Forest Theme - Scrollbar */\r\nbody.dark-forest .modal-body::-webkit-scrollbar-thumb,\r\nbody.dark-forest .app-modal-body::-webkit-scrollbar-thumb,\r\nbody.dark-forest .chart-modal-body::-webkit-scrollbar-thumb,\r\nhtml[data-theme=\"dark-forest\"] .modal-body::-webkit-scrollbar-thumb,\r\nhtml[data-theme=\"dark-forest\"] .app-modal-body::-webkit-scrollbar-thumb,\r\nhtml[data-theme=\"dark-forest\"] .chart-modal-body::-webkit-scrollbar-thumb {\r\n    background: var(--theme-accent-bg-soft, rgba(47, 107, 122, 0.22));\r\n    border-radius: 4px;\r\n}\r\n\r\nbody.dark-forest .modal-body::-webkit-scrollbar-thumb:hover,\r\nbody.dark-forest .app-modal-body::-webkit-scrollbar-thumb:hover,\r\nbody.dark-forest .chart-modal-body::-webkit-scrollbar-thumb:hover,\r\nhtml[data-theme=\"dark-forest\"] .modal-body::-webkit-scrollbar-thumb:hove